    const downloadExcel = () => {
        const worksheet = XLSX.utils.json_to_sheet(data);
        const workbook = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');
        const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
        saveAs(new Blob([excelBuffer], { type: 'application/octet-stream' }), 'data.xlsx');
    };

    const downloadCSV = () => {
        const csv = data.map((item) => Object.values(item).join(',')).join('\n');
        saveAs(new Blob([csv], { type: 'text/csv;charset=utf-8' }), 'data.csv');
    };

    const downloadPDF = () => {
        const doc = new jsPDF();
        doc.autoTable({ // Adding table to the PDF
            head: [Object.keys(data[0])], // Table headers
            body: data.map(obj => Object.values(obj)), // Table body
            headStyles: {
                fillColor: [238, 238, 119], // Background color of header
                textColor: [0, 0, 0] // Text color of header
            },
        });
        doc.save('data.pdf');
    };
